import PressSlider from 'press-ui/press-slider/press-slider.vue';
import PressToast from 'press-ui/press-toast/press-toast.vue';


export default {
  i18n: {
    'zh-CN': {
      title1: '基础用法',
      title2: '双滑块',
      title3: '指定选择范围',
      title4: '禁用',
      title5: '指定步长',
      customStyle: '自定义样式',
      customButton: '自定义按钮',
      text: '当前值：',
      vertical: '垂直方向',
    },
    'en-US': {
      title1: 'Basic Usage',
      title2: 'Dual thumb mode',
      title3: 'Range',
      title4: 'Disabled',
      title5: 'Step size',
      customStyle: 'Custom Style',
      customButton: 'Custom Button',
      text: 'Current value: ',
      vertical: 'Vertical',
    },
  },
  components: {
    PressSlider,
    PressToast,
  },
  data() {
    return {
      sectionStyle: 'margin: 12px 16px',
      valueMap: {
        basic: '50',
        range: [10, 50],
        maxAndMin: 50,
        customStyle: 50,
        customButton: 30,
      },
      dragValue: 50,
    };
  },
  methods: {
    onChange(val) {
      console.log('[onChange] val: ', val);
      this.onGTip(`${this.t('text')}${val.toString()}`);
    },
    onDrag(val) {
      console.log('[onDrag] val: ', val);
      this.onGTip(`drag: ${val}`);
      this.dragValue = val;
    },
  },
};
